/* Centering the container */
.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #f5f5f5; /* Soft background */
}

/* Styling the logo */
.logo {
    width: 200px; /* Adjust the siz e to fit your design */
    margin-bottom: 20px;
}

/* Form styling */
.login-form {
    margin-top:10vh;
    background-color: #fff;
    padding: 30px 45px 55px;
    border-radius: 10px;
    box-shadow: 2px 10px 20px rgba(0, 0, 0, 0.1);
    width: 300px;
}

.login-form h2{
    margin-bottom:20px;
}

.form-group {
    margin-bottom: 15px;
    text-align: left;
}

label {
    display: block;
    margin-bottom: 5px;
}

input {
    width: 93%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 14px;
}

.login-btn {
    background-color: #006666; /* Match logo color */
    color: white;
    padding: 10px;
    width: 100%;
    border: none;
    border-radius: 5px;
    margin-bottom:20px;
    font-size: 16px;
    cursor: pointer;
}

.login-btn:hover {
    background-color: #004d4d;
}
