/* Centering the container */
.signup-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #f5f5f5;
}

/* Styling the logo */
.logo {
    width: 200px;
    margin-bottom: 25px;
}

/* Form styling */
.signup-form {
    margin-top:10vh;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 2px 10px 20px rgba(0, 0, 0, 0.1);
    width: 400px;
}

.signup-form h2 {
    margin-bottom: 20px;
}

.form-group {
    margin-bottom: 15px;
    text-align: left;
}

input, select {
    width: 93%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 14px;
    margin-bottom: 10px;
}

.signup-btn {
    background-color: #006666;
    color: white;
    padding: 10px;
    width: 100%;
    border: none;
    border-radius: 5px;
    margin-bottom: 20px;
    font-size: 16px;
    cursor: pointer;
}

.signup-btn:hover {
    background-color: #004d4d;
}

/* Link styling */
.login-link {
    display: block;
    color: #006666;
    margin-top: 10px;
}
