/* Centering the entire container */
.role-selection-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #f5f5f5;
}

/* Styling the title */
.role-selection-container h1 {
    margin-top:20vh;
    font-size: 2rem;
    margin-bottom: 40px;
}

/* Styling the container for role options */
.roles-container {
    display: flex;
    justify-content: space-around;
    width: 35%; /* Adjust width for spacing between roles */
}

/* Styling each role option */
.role-option {
    text-decoration: none;
    color: black; /* Keep the text black */
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Styling the circles containing images */
.role-circle {
    width: 150px;
    height: 150px;
    background-color: #fff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
}

.role-img {
    width: 80%; /* Adjust image size */
    height: auto;
}

/* Styling the text below each image */
.role-option p {
    font-size: 1.2rem;
    font-weight: bold;
}
